.main {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
  padding-bottom: 20px;
  border-bottom: 1px solid #6a6a6a;
}


.main__items {
  font-size: 0;
}

.main__sidebar {
  width: 205px;
  margin-right: 20px;
  display: flex;
  flex-wrap: wrap;
  align-content: spase-between;
  order: -1;
}

.main__sidebar-text {
  width: 185px;
  line-height: 16px;
}

.main__sidebar-text p {
  margin: 0;
}

.main__sidebar img {
  align-self: flex-end;
}

.main__items .news {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 10px -10px 0 -10px;
}

.main__items .news__item {
  width: calc(50% - 20px);
  height: 200px;
  margin: 0 10px;
  background-color: #dcdcdc;
}

@media screen and (max-width: 960px) {
  .main {
    margin: 0 10px 20px 10px;
  }
}

@media screen and (max-width: 770px) {
  .main__items .news__item {
    width: calc(100% - 20px);
  }
  
  .main__items .news__item:nth-child(2) {
    margin-top: 20px;
  }
}

@media screen and (max-width: 640px) {
  .main {
    justify-content: center;
	flex-wrap: wrap;
  }
  
  .main__sidebar {
    order: 1;
	margin-right: 0;
  }
  
  .main__sidebar-text {
    margin: 20px 0;
  }
}