.gallery {
  margin: 10px -10px;
  display: flex;
  flex-wrap: wrap;
}

.gallery__item {
  width: calc(20% - 20px);
  height: 150px;
  margin: 10px;
  cursor: pointer;
  overflow: hidden;
}

@media screen and (max-width: 770px) {
  .gallery__item{
    width: calc(33.3% - 20px);
  }
}

@media screen and (max-width: 400px) {
  .gallery__item{
    width: calc(50% - 20px);
  }
}