body {
  font-size: 16px;
  font-family: "Lato", Arial, sans-serif;
  font-weight: 300;
  color: #000000;
  position: relative;
}
html {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
*,
*::after,
*::before {
  -webkit-box-sizing: inherit;
  box-sizing: inherit;
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
.container {
  position: relative;
  max-width: 960px;
  margin: 0 auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.head-h1 {
  font-size: 26px;
  line-height: 26px;
  font-weight: 600;
  text-transform: uppercase;
  text-align: center;
  margin-top: 60px;
}
.main {
  overflow: hidden;
}
/*base end*/
.header {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin: 20px 0;
  padding-bottom: 20px;
  border-bottom: 1px solid #6a6a6a;
}
.header__logo {
  width: 125px;
}
.header__slogan {
  width: 280px;
  text-align: center;
  line-height: 22px;
}
.header__phone,
.header__mail,
.header__address {
  line-height: 16px;
}
.header__contacts p {
  margin: 10px 0;
}
@media screen and (max-width: 960px) {
  .header {
    margin: 20px 10px;
  }
}
@media screen and (max-width: 820px) {
  .header {
    justify-content: center;
  }
  .header__slogan {
    margin: 0 20px;
  }
  .header__contacts {
    text-align: center;
    padding-top: 10px;
  }
}
.main {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
  padding-bottom: 20px;
  border-bottom: 1px solid #6a6a6a;
}
.main__items {
  font-size: 0;
}
.main__sidebar {
  width: 205px;
  margin-right: 20px;
  display: flex;
  flex-wrap: wrap;
  align-content: spase-between;
  order: -1;
}
.main__sidebar-text {
  width: 185px;
  line-height: 16px;
}
.main__sidebar-text p {
  margin: 0;
}
.main__sidebar img {
  align-self: flex-end;
}
.main__items .news {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 10px -10px 0 -10px;
}
.main__items .news__item {
  width: calc(50% - 20px);
  height: 200px;
  margin: 0 10px;
  background-color: #dcdcdc;
}
@media screen and (max-width: 960px) {
  .main {
    margin: 0 10px 20px 10px;
  }
}
@media screen and (max-width: 770px) {
  .main__items .news__item {
    width: calc(100% - 20px);
  }
  .main__items .news__item:nth-child(2) {
    margin-top: 20px;
  }
}
@media screen and (max-width: 640px) {
  .main {
    justify-content: center;
    flex-wrap: wrap;
  }
  .main__sidebar {
    order: 1;
    margin-right: 0;
  }
  .main__sidebar-text {
    margin: 20px 0;
  }
}
.gallery {
  margin: 10px -10px;
  display: flex;
  flex-wrap: wrap;
}
.gallery__item {
  width: calc(20% - 20px);
  height: 150px;
  margin: 10px;
  cursor: pointer;
  overflow: hidden;
}
@media screen and (max-width: 770px) {
  .gallery__item {
    width: calc(33.3% - 20px);
  }
}
@media screen and (max-width: 400px) {
  .gallery__item {
    width: calc(50% - 20px);
  }
}
.footer {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-bottom: 20px;
  margin: 0 -10px;
  min-height: 200px;
}
.footer__item {
  width: calc(33.3% - 20px);
  height: 180px;
  margin: 0 10px;
  background-color: #777777;
}
.footer .footer__item:nth-child(3) {
  background-color: transparent;
}
.footer__item-3 {
  display: flex;
  flex-wrap: wrap;
}
.footer__item-narrow {
  background-color: #777777;
  height: 75px;
  width: 100%;
}
.footer__item-narrow:nth-child(2) {
  align-self: flex-end;
  background-image: url(../img/13.jpg);
  background-size: cover;
}
@media screen and (max-width: 970px) {
  .footer {
    margin: 0 10px;
  }
}
@media screen and (max-width: 770px) {
  .footer {
    justify-content: center;
  }
  .footer__item {
    width: calc(50% - 20px);
    margin: 0 10px 20px 10px;
  }
}
@media screen and (max-width: 500px) {
  .footer__item {
    width: 100%;
  }
}
.popup {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  display: none;
  text-align: center;
}
.popup_bg {
  background: rgba(0, 0, 0, 0.4);
  position: absolute;
  z-index: 1;
  height: 100%;
  width: 100%;
}
.popup_img {
  position: relative;
  margin: 0 auto;
  z-index: 2;
  max-height: 94%;
  max-width: 94%;
  margin: 1% 0 0 0;
}
