body {
  font-size: 16px;
  font-family: "Lato", Arial, sans-serif;
  font-weight: 300;
  color: #000000;
  position: relative;
}

html {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

*, *::after, *::before {
  -webkit-box-sizing: inherit;
  box-sizing: inherit;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

.container {
  position: relative;
  max-width: 960px;
  margin: 0 auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.head-h1 {
  font-size: 26px;
  line-height: 26px;
  font-weight: 600;
  text-transform: uppercase;
  text-align: center;
  margin-top: 60px;
}

.main {
  overflow: hidden;
}

@media screen and (min-width: 768px) {
}

/*base end*/
