.header {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin: 20px 0;
  padding-bottom: 20px;
  border-bottom: 1px solid #6a6a6a;
}

.header__logo {
  width: 125px;  
}

.header__slogan {
  width: 280px;
  text-align: center;
  line-height: 22px;
}

.header__phone, .header__mail, .header__address {
  line-height: 16px;
}

.header__contacts p {
  margin: 10px 0;
}

@media screen and (max-width: 960px) {
  .header {
    margin: 20px 10px;
  }
}

@media screen and (max-width: 820px) {
  .header {
    justify-content: center;
  }
  
  .header__slogan {
    margin: 0 20px;
  }
  
  .header__contacts {
    text-align: center;
    padding-top: 10px;
  }
}
